@import url(https://fonts.googleapis.com/css?family=Rubik:300,regular,500,600,700,800,900,300italic,italic,500italic,600italic,700italic,800italic,900italic);
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'RubiK', sans-serif;
}

body {
    background: #F7E4E8;
}
html {
    scroll-behavior: smooth;
}

.wrapper {
    padding: 20px;
    margin: 0 auto;
    max-width: 2000px;
}

.homePage-wrapper {
    padding-top: 180px;
    background: #fff;

    height: calc(100vh - 20px);
}
.aboutPage-wrapper {
    background: #000;
    height: 100%;
    position: relative;
}
.catalogPage-wrapper {
    background-color: #fff;
    height: 100%;
}
.contactPage-wrapper {
    background-color: #fff;
    height: 100%;
}
.footer-wrapper {
    background: #fff;
    height: 100%;
}

.hText {
    margin-left: 40px;
    max-width: 550px;
}

.fLine {
    font-size: 64px;
    font-weight: bold;
    line-height: 64px;
    letter-spacing: 0.25rem;
}
.sLine {
    font-weight: 300;
    font-size: 36px;
    line-height: 36px;
    letter-spacing: 0.035rem;
    margin: 10px 0 30px;
}
.secondary {
    font-size: 16px;
    line-height: 16px;
    color: #828180;
}

.fPageBtnWrapper {
    margin-top: 50px;
}

.landBtn {
    cursor: pointer;
    width: 320px;
    height: 70px;
    text-decoration: none;

    display: flex;
    position: relative;
    margin: 40px 0;

    background: #E38B9B;
    border-radius: 2px 35px 35px 35px;
}
.landText {
    color: #fff;
    margin: auto 0;
    padding: 15px;
    font-weight: 550;
    font-size: 24px;
    line-height: 24px;
}
.landTime {
    font-size: 12px;
    line-height: 12px;
    font-weight: 350;
    color: #fff;
    position: absolute;
    right: 25px;
    bottom: 7px;
}

/* Image */
.img__box {
    width: 100%;
    padding: 0 13px;
    padding-top: 20px;
}
.img {
    height: calc(100vh - 80px);
    border: 1px solid #dfdfdf;
    border-radius: 10px;
    box-sizing: border-box;
}
.bottom__wrapper {
    display: flex;
    margin: 0 30px;
    margin-top: 10px;
    justify-content: space-between;
}
.description {
    font-size: 12px;
    line-height: 130%;
    color: #a9a9a9;
    width: 50%;
}
.author {
    font-weight: 550;
    font-size: 12px;
    line-height: 130%;
    text-align: right;
    color: #afafaf;
    width: 50%;
}

.sectionName {
    color: #fff;
    font-weight: bold;
    font-size: 32px;
    line-height: 32px;
    letter-spacing: 0.025em;
    position: absolute;
    top: 80px;
    left: 40px;
    text-shadow: 2px 2px 1px black;
}
.fLineText {
    color: #fff;
    position: absolute;
    top: 140px;
    left: 50px;
    font-size: 20px;
    line-height: 20px;
    text-shadow: 2px 2px 1px black;
}
.aboutText {
    color: #fff;
    margin-left: 50px;
    margin-top: 100px;
    width: 60%;
    max-width: 800px;
    padding-bottom: 20px;
}

.aboutTextStand {
    margin-top: 30px;
}

.aboutTextMiddle {
    margin-top: 40px;
}


.pageName {
    font-size: 32px;
    line-height: 32px;
    font-weight: bold;
    letter-spacing: 0.025em;
}

@media (max-width: 800px) {
    .nav__buttons ul li {
        font-size: 16px;
    }
}
@media (max-width: 700px) {
    .nav__buttons {
        visibility: hidden;
        display: none;
    }
}

.cartItems {
    padding: 0 30px;
}
.items-list {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    list-style-type: none;
}

.item {
    margin: 0;
    padding: 0 1.75rem 3rem 0;
}
.item__wrapper {
    width: 280px;
    text-align: center;
}
.item__overlay {
    cursor: pointer;
    text-decoration: none;
    color: #000;
    width: 100%;
    transition: 0.15s ease-in-out;
}
.item__overlay img {
    transition: 0.15s ease-in-out;
}
.item__overlay:hover img {
    transform: translateY(5px);
}
.item__overlay:hover {
    color: #007bff;
}
.img__container{
    width: 200px;
    height: 200px;
    margin: 0 auto;
    overflow: hidden;
}
.img__container img {
    width: 100%;
    
}
.item__details {
    padding: 0 10px;
}
.item__brand {
    margin-top: 15px;
    font-size: 14px;
    letter-spacing: 2.5px;
    text-transform: uppercase;
    font-weight: 400;
}
.item__name {
    font-size: 16px;
    line-height: 24px;
    margin: 5px 0;
    font-weight: 700;
    text-transform: uppercase;
}
.item__type {
    font-size: 14px;
    line-height: 14px;
    color: #828180;
}
.item__bottom {
    margin-top: 10px;
    padding: 0 1rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.item__bottom .price {
    font-size: 18px;
    line-height: 20px;
    font-weight: 550;
}
.btnGroup {
    display: flex;
    width: 40%;
    justify-content: space-between;

}
.btnGroup button {
    cursor: pointer;
    border: none;
    background: #fff;
    padding: 7px 7px 4px 7px;
}

.addToCart:hover .cartBtn {
    transition: 0.15s ease-in-out;
    color: #007bff;
}
.toWishlist:hover .smallFragileHeart {
    color: red;
    transition: 0.15s ease-in-out;
}
.favorite {
    color: red;
}
.toWishlist:hover .favorite {
    transition: 0.15s ease-in-out;
    color: rgb(255, 100, 100);
}

.header {
    padding: 70px 20px;
    display: flex;
    place-items: center;
    justify-content: space-between;
}


.button {
    cursor: pointer;
    border: none;
    outline: none;
    background: #fff;
    font-size: 18px;
    padding: 7px 16px;
    
}
.active {
    background: #F5D6DC;
}


.buttonGroup button:first-child {
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
}
.buttonGroup button:last-child {
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
}

.catalogBtns {
    display: flex;
    width: 100%;
    justify-content: flex-end;
    margin-right: 50px;
}

.toggles {
    position: relative;
    display: flex;
}
.sortType {
    margin-right: 20px;
}

.filler {
    /* display: none; */
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    width: 110px;
    height: 32px;
    background: #F5D6DC;
    border-top-right-radius: 4px;
    border-bottom-right-radius: 4px;
    transition: transform 0.2s ease-in-out;
    transform: translateX(100%);
}

.toggles input {
    display: none;
}
.toggles label {
    z-index: 2;
    cursor: pointer;
    width: 110px;
    height: 32px;
    line-height: 32px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    flex: 1 1;
    border-radius: 5px;
    text-align: center;
}
.offset {
    transform: translateX(0%);
    border-top-left-radius: 4px;
    border-bottom-left-radius: 4px;
    border-top-right-radius: 0px;
    border-bottom-right-radius: 0px;
}
.itemInfo {
    text-align: left;
}
.itemInfo__topbar {
    height: 60px;
    display: flex;
    justify-content: flex-end;
}
.info-wrapper {
    cursor: pointer;
    width: 40px;
    height: 40px;
    border: none;
    background: #fff;
    margin: 20px;
}
.info-wrapper:hover svg {
    color: #06c;
    transition: 0.15s ease-in-out;
}

.shareBtn {
    height: 30px;
    width: 28px;
}
.closeBtn {
    width: 40px;
    height: 40px;
}

.details {
    margin: 20px;
    display: flex;
    justify-content: space-between;
}
.details__mainInfo {
    text-align: left;
    max-width: 690px;
}

.top {
    display: inline-flex;
}
.overlay__img {
    max-height: 100%;
}

.top__left {
    max-height: 360px;
    width: 310px;
}
.top__right {
    max-height: 360px;
}
.images {
    height: 310px;
    display: flex;
    align-items: flex-end;
    justify-content: center;
}
.img__slider-bottom {
    height: 30px;
    display: inline-flex;
    justify-content: center;
    width: 100%;
    margin-top: 20px;
}
.item__brandName {
    font-weight: 550;
    line-height: 100%;
    height: 52px;
    font-size: 24px;
    text-align: left;
    width: 380px;
    overflow-x: auto;
}
.itemO__type {
    margin: 20px 0;
    font-size: 14px;
    line-height: 14px;
    color: #828180;
    text-align: left;
}
.itemO__sizes-wrapper {
    background-color: #ddd;
    border-radius: 10px;
    width: 380px;
    display: flex;
    justify-content: start;
    flex-wrap: wrap;
    padding-bottom: 20px;
}

.overlay__size {
    margin: 20px 0 0 20px;
    border-radius: 5px;
    text-align: center;
    line-height: 60px;
    font-size: 20px;
    background-color: #fff;
    width: 160px;
    height: 60px;
    cursor: pointer;
    transition: 0.15s ease-in-out;
}
.overlay__size input {
    display: none;
}
.overlay__size:hover {
    background-color: #f5d6dc;
    font-size: 21px;
}
.overlay__size-selected {
    background-color: #f5d6dc;
    font-size: 21px;
}
.item__sizes {
    /* padding: 20px; */
    height: 100%;
}
.item__price {
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 0.02rem;
    font-weight: 550;
}
.itemO__addToCart {
    cursor: pointer;

    border-radius: 10px;
    background-color:#E38B9B;
    border: none;
    width: 140px;
    height: 50px;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.02rem;
    color: #fff;
    transition: 0.15s ease-in-out;
}
.itemO__addToCart:hover {
    background-color: #d87788;
    
}
.item__details-wrapper {
    text-align: left;
    padding: 20px;
    margin-top: 20px;
    font-size: 18px;
    line-height: 18px;
    letter-spacing: 0.02rem;
    background-color: #ddd;
    border-radius: 10px;
    max-width: 690px;
}
.itemO__bottom {
    display: inline-flex;
    justify-content: space-between;
    place-items: center;
    width: 100%;
    margin-top: 20px;
}
.item__country {
    margin: 0 0 10px;
}
.item__scent {
    margin-bottom: 10px;
}
.item__description {
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0.01rem;
    height: 180px;
    max-width: 800px;
    overflow: hidden;
    overflow-y: auto;
}

.details__notes-wrapper {
    min-width: 300px;
}
.noteGroup__list {
    list-style-type: none;
}
.noteGroup__name {
    font-size: 16px;
    line-height: 16px;
    font-weight: 550;
    letter-spacing: 0.02rem;
    margin-bottom: 15px;
}
.details__notes:not(:first-child) {
    margin-top: 20px;
}
.noteGropu__list-el {
    margin-bottom: 10px;
    max-width: 350px;
}
.contactPage {
    margin-top: 3px;
    padding-bottom: 50px;
}
.text {
    font-size: 16px;
    line-height: 16px;
    margin-right: 7px;
}
.typeOfContact {
    margin-left: 70px;
}
.typeBox {
    display: flex;
    place-items: center;
    margin: 40px 0;
}
.tel {
    text-decoration: none;
}
.chatMessage {
    width: 211px;
    display: flex;
    position: relative;
    align-items: center;
    margin: 40px 0;

    background: #E38B9B;
    border-radius: 1px 10px 10px 10px;
}

.preText {
    color: #fff;
    padding: 9px;
    font-size: 14px;
    line-height: 14px;
}
.currTime {
    font-size: 12px;
    line-height: 12px;
    font-weight: 350;
    color: #fff;
    position: absolute;
    right: 15px;
    bottom: 5px;
}

.contactList {
    list-style-type: none;
    display: flex;
    justify-content: space-between;
    width: 450px;
}
.contactList li {
    cursor: pointer;
    display: flex;
    place-items: center;
    transition: 0.35s ease-in-out;
}
.contactList a{
    border-radius: 50%;
    overflow: hidden;
    display: flex;
    place-items: center;

    width: 50px;
    height: 50px;
}
.contactList svg {
    margin: auto auto;
    width: 35px;
    height: 35px;
    color: white;
}
.contactList li:hover {
    transform: scale(1.15);
}

.icon {
    color: white;
}
.footerLinks-wrapper {
    margin-left: 50px;
    padding-bottom: 30px;
}
.footer {
    border-top: 1px solid #afafaf;
}
.listName {
    font-size: 24px;
    line-height: 24px;
    letter-spacing: 0.01em;
}
.footerLinks {
    list-style-type: none;
    margin-left: 10px;
}
.footerLinks li {
    margin: 20px 0;
    color: #007bff;
}
.link {
    font-size: 16px;
    flex-shrink: 16px;
    letter-spacing: 0.01em;
    transition: 0.15s ease-in-out;
    color: #06c;
}
.link:hover {
    color: red;
}

.copyright {
    margin-top: 60px;
    font-size: 18px;
    line-height: 18px;
    font-weight: 300;
    letter-spacing: 0.01em;
}
.nav-m {
    display: flex;
    width: 100%;
    padding: 7px 20px;
    justify-content: center;
    place-items: center;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    -webkit-backdrop-filter: saturate(180%) blur(20px);
            backdrop-filter: saturate(180%) blur(20px);
    background-color: rgba(255,255,255,0.72);
    z-index: 10;
    border-bottom: 1px solid rgb(215, 215, 215);;
}

.logo {
    font-weight: 900;
    font-size: 32px;
}

.nav__buttons {
    display: flex;
    justify-content: center;
    width: 100%;
}

.nav__buttons ul{
    list-style: none;
    display: flex;
    place-items: center;
    width: 100%;
    max-width: 750px;
    padding: 0 20px;
    justify-content: space-between;
}
.nav__buttons a {
    text-decoration: none;
    color: black;
}
.nav__buttons li {
    /* margin: 0 50px; */
    font-size: 20px;
    font-weight: bold;
}

.cart__button {
    display: inline-flex;
    align-items: center;
    background: #E38B9B;
    margin-left: 30px;
    border-radius: 8px;
    border: none;
    padding: 0 30px;
    height: 40px;
    transition: 0.15s ease-in-out;

    color: #fff;
    font-weight: 500;
    font-size: 18px;

    cursor: pointer;
}

.cart__button:hover {
    background: #d87788;
}
.cart__divider {
    width: 1px;
    height: 20px;
    background: #fff;
    margin: 0 10px;
}
.cart__count-wrapper {
    display: inline-flex;
    place-items: center;
    width: 24px;
    overflow: hidden;
}
.cart__count-wrapper svg {
    width: 20px;
    height: 20px;
}
.cart__count-icon {
    position: absolute;
    transform: translateX(-50%);
    opacity: 0;
}
.cart__button:hover .cart__count-text {
    opacity: 0;
    transition: 0.25s ease-in-out;
}
.cart__button:hover .cart__count-icon {
    opacity: 1;
    transform: translateX(-10%);
    transition: 0.25s ease-in-out;
}
.cart__count-text {
    text-align: left;
    font-weight: 400;
    font-size: 18px;
    line-height: 24px;
    width: 24px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.wishlist-wrapper {
    display: inline-flex;
    place-items: center;
}
.wishlist__button {
    cursor: pointer;
    width: 40px;
    height: 40px;
    border: none;
    background-color: rgba(255, 255, 255, 0);
}

.wishlist__button-icon {
    transform: scale(0.8);
}
.wishlist__button-icon-active {
    max-width: 40px;
    max-height: 40px;
    color: #e31b23
}
.wishlist__button:hover .wishlist__button-icon-active {
    color: #98002e;
    transition: 0.15s ease-in-out;
}
.wishlist__label {
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    line-height: 40px;
    overflow: hidden;
}
.wishlist__label-text {
    transform: translateX(0%);
    cursor: pointer;
    transition: 0.35s ease-in-out;
}
.wishlist__label-text-hidden {
    transform: translateX(-100%);
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
}


